<template>
    <div class="presidencia">
        <transition :name="transition_class">
            <div
                v-if="current_magistrado && !is_mobile"
                :key="current_magistrado.nombre"
                :class="current_magistrado.cssClass.container"
            >
                <span :class="current_magistrado.cssClass.titulo">{{
                    current_magistrado.title
                }}</span>
                <span :class="current_magistrado.cssClass.magistrado">{{
                    current_magistrado.magistrado
                }}</span>
                <p :class="current_magistrado.cssClass.nombre">
                    {{ current_magistrado.nombre }}
                </p>
                <img
                    :class="current_magistrado.cssClass.foto"
                    :src="'/assets/images/' + current_magistrado.foto"
                    alt="magistrado"
                    loading="lazy"
                />

                <span
                    v-for="(cargo, index) in current_magistrado.cargo"
                    :class="[
                        current_magistrado.cssClass.cargos,
                        {
                            cargo__margin:
                                index + 1 == current_magistrado.cargo.length
                        }
                    ]"
                    :key="cargo"
                    >{{ cargo }}</span
                >

                <span :class="current_magistrado.cssClass.funcion">{{
                    current_magistrado.funcion
                }}</span>

                <span :class="current_magistrado.cssClass.comision">{{
                    current_magistrado.comision
                }}</span>
            </div>
        </transition>

        <template v-if="is_mobile">
            <div
                v-for="(magistrado, index) in magistrados"
                :key="magistrado.nombre"
                :class="['presidencia__mobile-container']"
            >
                <span
                    v-if="index == 0"
                    :class="current_magistrado.cssClass.titulo"
                    >{{ current_magistrado.title }}</span
                >
                <span :class="magistrado.cssClass.magistrado">{{
                    magistrado.magistrado
                }}</span>
                <span :class="magistrado.cssClass.nombre">
                    {{ magistrado.nombre }}
                </span>
                <div class="row__mobile">
                    <div class="column__mobile--centrado">
                        <img
                            :class="magistrado.cssClass.foto"
                            :src="'/assets/images/' + magistrado.foto"
                            alt="magistrado"
                            loading="lazy"
                        />
                    </div>
                    <div class="column__mobile">
                        <span
                            v-for="(cargo, index) in magistrado.cargo"
                            :class="[
                                magistrado.cssClass.cargos,
                                {
                                    cargo__margin:
                                        index + 1 == magistrado.cargo.length
                                }
                            ]"
                            :key="cargo"
                            >{{ cargo }}</span
                        >

                        <span :class="magistrado.cssClass.funcion">{{
                            magistrado.funcion
                        }}</span>

                        <span :class="magistrado.cssClass.comision">{{
                            magistrado.comision
                        }}</span>
                    </div>
                </div>
            </div>
        </template>
    </div>
</template>

<script>
export default {
    name: "presidencia",
    data() {
        return {
            transition_class: "SlideInXRight",
            is_mobile: false,
            magistrados: [
                // Presidencia
                {
                    cssClass: {
                        container: "presidencia__content-magistrado",
                        titulo: "title_presidencia",
                        magistrado: "title_magistrado magistrado",
                        nombre: "nombre_magistrado",
                        foto: "foto_magistrado",
                        cargos: "cargo__presidencia",
                        funcion: "funcion",
                        comision: "comision"
                    },
                    title: "Presidencia",
                    magistrado: "Magistrado",
                    nombre: "Álvaro Namén Vargas",
                    foto: "Mag Álvaro Namén Vargas.jpg",
                    cargo: [
                        "Presidente del consejo de estado",
                        "Presidente de la Sala Plena"
                    ],
                    funcion: "Sala de Consulta y Servicio Civil",
                    comision:
                        "Integrante de todas las comisiones de trabajo de Sala Plena."
                }
            ],
            magistrado_index: 0
        }
    },
    methods: {
        right_callback() {
            this.magistrado_index += 1
        },

        left_callback() {
            this.magistrado_index -= 1
        },

        handleResize() {
            const isMobile = window.matchMedia(
                "(max-width: 720px), (max-height: 410px)"
            )
            this.is_mobile = isMobile.matches ? true : false
        }
    },
    computed: {
        current_magistrado() {
            return this.magistrados[this.magistrado_index]
        }
    },
    watch: {
        magistrado_index: function(newVal, oldVal) {
            if (newVal > oldVal) {
                this.transition_class = "SlideInXRight"
            } else {
                this.transition_class = "SlideInXLeft"
            }
        }
    },
    mounted() {
        window.addEventListener("resize", this.handleResize)
        this.handleResize()
    },
    beforeDestroy() {
        window.removeEventListener("resize", this.handleResize)
    }
}
</script>

<style lang="scss" src="./presidencia.scss" scoped></style>
